/* eslint-disable */
import Vue from 'vue';
import Router from 'vue-router';
import App from '../App';

Vue.use(Router);

export default new Router({
  mode: 'history',
  routes: [
    {
      path: '/',
      name: 'home',
      component: App,
    },
  ],
});



